<script>
// import { api, get, post } from "@/service";
import { createUser, callUserData, updateCallUser } from '@/service/customer'
// import { mapState } from 'vuex'
import { formatDate, isNull,checkPhoneNum } from '@/utils'

export default {
  name: 'AddContact',
  data() {
    return {
      showMoreBox: false,
      selectStartTime: false,
      contactsBirth: '',
      maxDate: new Date(),
      minDate: new Date(1900, 1, 1),
      currentTime: new Date(),
      userName: '',
      companyName: '',
      duty: '',
      mobile: '',
      sex:'请选择', // 性别
      contactNumber: -1, // 第几联系人
      user: {
        companyName: this.$store.getters.userData.companyName,
        contactsName:'',
        contactsPost: '',
        contactsMoblie: '',
        contactsEmail: '',
        contactsSex: '',
        contactsBirth: '',
        contactsQq: '',
        contactsLike: '',
        contactsAdress: '',
        contactsRemark: '',
        externalContactIds:'',
        contactsFixedTelephone:''
      },
      openType:2
    }
  },
  computed: {},
  watch: {},
  created() {
    this.openType = this.$route.query.openType || 3
    if (this.$route.query.id) {
      this.getOpenUser(this.$route.query.id)
    }
  },
  methods: {
    getUserId(){
      let that = this
      let userIdss = ''
      wx.invoke('selectExternalContact', 
        {
          "filterType": 0, //0表示展示全部外部联系人列表，1表示仅展示未曾选择过的外部联系人。默认值为0；除了0与1，其他值非法。在企业微信2.4.22及以后版本支持该参数
          "mode": "single",
          "type": ["user"],
        }, function(res){
          console.log('表示展示全部外部联系人列表',res)  
        if(res.err_msg == "selectExternalContact:ok"){    
            userIdss = res.userIds.toString(';')                 
            that.user.externalContactIds = userIdss
        }else {
          // alert('外部联系人列表为空 或 获取联系人失败')
            //错误处理
        }
      })
    },
    getOpenUser(id) {
      callUserData({ id }).then((res) => {
        if(res.data.lendingItem){
         res.data.lendingItem.createTime = new Date(res.data.lendingItem.createTime)
         res.data.lendingItem.createTime = formatDate(res.data.lendingItem.createTime,'yy-MM-dd')
         if(res.data.lendingItemList){
           res.data.lendingItemList.map(d=>{
             d.createTime = new Date(d.createTime)
             d.createTime = formatDate(d.createTime,'yyyy-MM-dd')
           })
         }
        }
        // console.log(res)
        this.user = res.data
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    showMore() {
      this.showMoreBox = true
    },
    startTimeClick() {
      this.selectStartTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.user.contactsBirth = `${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectStartTime = false
      }
    },
    // addBut
    addBut() {
      let txts = {                                                        
        contactsPost:'职务',
        contactsName:'姓名',
      } 
      let params = this.user
      if(!this.$checkFrom(params,txts)){
        return
      } 
      if (!checkPhoneNum(params.contactsMoblie)) {
        this.$notify('请确认第一联络人手机号码')
        return
      }
      
      if (this.openType==2) { this.updated() } else { this.createUsered() }
    },
    // 新增联系人
    createUsered() {
      const params = this.user
      // console.log('params', params)
      // this.$Clog(params);      
      createUser(params).then((res) => {
        this.$Clog(res)
        this.$notifySty('操作成功')
        this.$router.go(-1)
      })
    },
    // 修改联系人
    updated() {
      const params = this.user
      // console.log('修改联系人', params)
      if (this.$checkFrom(params)) {
        updateCallUser(params).then((res) => {
          this.$Clog(res)
          this.$notifySty('操作成功')
          this.$router.go(-1)
        })
      }
    },

  },
}
</script>
